import $ from 'jquery';
import Swal from 'sweetalert2';
import BpmnModeler from 'bpmn-js/lib/Modeler';

var diagramXML = $('#diagramaBaixado').val();
var diagramSVG = '';
var diagramaModificado;
var container = $('#js-drop-zone');

var modeler = new BpmnModeler({
  container: '#js-canvas'
});

function createNewDiagram() {
  openDiagram(diagramXML);
}

function openDiagram(xml) {

  modeler.importXML(xml, function(err) {

    if (err) {
      container
        .removeClass('with-diagram')
        .addClass('with-error');

      container.find('.error pre').text(err.message);

      console.error(err);
    } else {
      container
        .removeClass('with-error')
        .addClass('with-diagram');
    }


  });
}

function saveSVG(done) {
  modeler.saveSVG(done);
}

function saveDiagram(done) {

  modeler.saveXML({ format: true }, function(err, xml) {
    diagramaModificado = xml;
    done(err, xml);
  });
}

function registerFileDrop(container, callback) {

  function handleFileSelect(e) {
    e.stopPropagation();
    e.preventDefault();

    var files = e.dataTransfer.files;

    var file = files[0];

    var reader = new FileReader();

    reader.onload = function(e) {
      var xml = e.target.result;
      callback(xml);
    };

    reader.readAsText(file);
  }

  function handleDragOver(e) {
    e.stopPropagation();
    e.preventDefault();

    e.dataTransfer.dropEffect = 'copy'; // Explicitly show this is a copy.
  }

  container.get(0).addEventListener('dragover', handleDragOver, false);
  container.get(0).addEventListener('drop', handleFileSelect, false);
}


// file drag / drop ///////////////////////

// check file api availability
if (!window.FileList || !window.FileReader) {
  window.alert(
    'Looks like you use an older browser that does not support drag and drop. ' +
    'Try using Chrome, Firefox or the Internet Explorer > 10.');
} else {
  registerFileDrop(container, openDiagram);
}

// bootstrap diagram functions

$(function() {
  createNewDiagram();
  var downloadLink = $('#js-download-diagram');
  var downloadSvgLink = $('#js-download-svg');

  $('.entry a').click(function(e) {
    if (!$(this).is('.active')) {
      e.preventDefault();
      e.stopPropagation();
    }
  });

  function setEncoded(link, name, data) {
    var encodedData = encodeURIComponent(data);
    if (data) {
      link.addClass('active').attr({
        'href': 'data:application/bpmn20-xml;charset=UTF-8,' + encodedData,
        'download': name
      });
    } else {
      link.removeClass('active');
    }
  }

  var exportArtifacts = debounce(function() {
    $('#download-diagram').show();
    $('#download-svg').show();
    saveSVG(function(err, svg) {
      diagramSVG = svg;
      setEncoded(downloadSvgLink, 'diagram.svg', err ? null : svg);
    });

    saveDiagram(function(err, xml) {
      setEncoded(downloadLink, 'diagram.bpmn', err ? null : xml);
    });
  }, 500);

  $('#btnSalvarDiagrama').click(function(e) {
    $('#btnSalvarDiagrama').removeClass('fa-save').addClass('fa-spinner fa-spin');
    Swal.fire({
      title: 'Voce deseja salvar o diagrama?',
      text: 'Esta operacao nao podera ser revertida',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Salvar',
      cancelButtonText: 'Cancear'
    }).then((result) => {
      let codmodelodiagramatico = $('#codmodelodiagramatico').val();
      if (result.value) {

        $.ajax({
          url: 'gravar',
          type: 'POST',
          cache: false,
          dataType: 'json',
          headers: {
            'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content'),
          },
          data: {
            'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content'),
            strXml: diagramaModificado,
            strSvg: diagramSVG,
            codmodelodiagramatico: codmodelodiagramatico,
          },
          success: function() {
            Swal.fire({
              type: 'success',
              title: 'Diagrama Salvo com sucesso!',
              timer: 1500,
            });
            $('#btnSalvarDiagrama').removeClass('fa-spinner fa-spin').addClass('fa-save');
          },
          error: function() {
            Swal.fire({
              type: 'error',
              title: 'Erro ao tentar salvar o digrama',
              timer: 1500,
            });
            $('#btnSalvarDiagrama').removeClass('fa-spinner fa-spin').addClass('fa-save');
          },
        });
      } else {
        $('#btnSalvarDiagrama').removeClass('fa-spinner fa-spin').addClass('fa-save');
      }
    });
  });
  modeler.on('commandStack.changed', exportArtifacts);
});



// helpers //////////////////////

function debounce(fn, timeout) {

  var timer;

  return function() {
    if (timer) {
      clearTimeout(timer);
    }

    timer = setTimeout(fn, timeout);
  };
}